import React from 'react';
import { Link } from 'react-router-dom';

export default function Footer({ copyright, brand, routes = [] }) {
	return (
		<div className='bg-dark'>
			<footer className='container d-flex flex-wrap justify-content-between align-items-center py-3 border-top'>
				<p className='col-md-4 mb-0 text-muted'>{copyright}</p>

				<a
					href='/'
					className='col-md-4 d-flex align-items-center justify-content-center mb-3 mb-md-0 me-md-auto text-decoration-none text-white'
				>
					{brand}
				</a>

				<ul className='nav col-md-4 justify-content-end'>
					{routes.map(({ name, path }) => (
						<li key={name} className='nav-item'>
							<Link to={path} className='nav-link px-2 text-muted'>
								{name}
							</Link>
						</li>
					))}
				</ul>
			</footer>
		</div>
	);
}
