export const getFilteredFields = (data = [], omit_array = []) =>
	data?.filter(datum => !omit_array.includes(datum));

export const getChunkedArray = (
	array = [],
	chunkSize = Math.round(array.length / 2),
) => {
	const chunkArray = [];
	for (let i = 0; i < array.length; i += chunkSize) {
		const chunk = array.slice(i, i + chunkSize);
		chunkArray.push(chunk);
	}
	return chunkArray;
};
