import { useEffect, useState } from 'react';

export default function useApi(call, params, instant = true) {
	const [loading, setLoading] = useState(instant);
	const [data, setData] = useState();
	const [error, setError] = useState();

	useEffect(() => {
		if (instant) getData();
		return () => {};
	}, []);

	const getData = async (refresh = false) => {
		if (!refresh) setLoading(true);
		const [d, e] = await call(params);
		if (e) setError(e);
		else setData(d);
		if (!refresh) setLoading(false);
		return [d, e];
	};

	const refresh = async () => {
		setLoading(true);
		const [d, e] = await getData(true);
		if (e) {
			setError(e);
			setData(null);
			setLoading(false);
			return null;
		}
		setError(null);
		setData(d);
		setLoading(false);
		return d;
	};

	return { data, loading, error, refresh };
}
