import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/navbar.css';

export default function Navbar({ routes = [], brand, activePath, logout }) {
	return (
		<nav className='navbar navbar-dark bg-dark navbar-expand-lg'>
			<div className='container-fluid container'>
				{/* Brand */}
				<a className='navbar-brand' href='/'>
					{brand}
				</a>
				{/* menu */}
				<button
					className='navbar-toggler'
					type='button'
					data-bs-toggle='collapse'
					data-bs-target='#navbarSupportedContent'
					aria-controls='navbarSupportedContent'
					aria-expanded='false'
					aria-label='Toggle navigation'
				>
					<span className='navbar-toggler-icon'></span>
				</button>
				{/* content - links */}
				<div
					className='collapse navbar-collapse navbar__content'
					id='navbarSupportedContent'
				>
					<ul className='navbar-nav me-auto mb-2 mb-lg-0 navbar__items'>
						{routes.map(({ name, path, onClick }) => (
							<li key={name} className='nav-item navbar__item'>
								<Link
									className={`nav-link ${
										activePath === path && 'active'
									} text-center`}
									aria-current='page'
									to={path}
								>
									{name}
								</Link>
							</li>
						))}
						<li className='nav-item navbar__item'>
							<p
								className='nav-link rounded bg-danger text-white text-center'
								onClick={logout}
							>
								Logout
							</p>
						</li>
					</ul>
				</div>
			</div>
		</nav>
	);
}
