import React from 'react';
import { useParams } from 'react-router-dom';
import { getDriveApplication, postDriveApplication } from '../api/drive';
import DynamicForm from '../components/DynamicForm';
import useApi from '../hooks/useApi';
import LoadingScreen from './LoadingScreen';

export default function DriveApplication() {
	const { id: driveId } = useParams();
	const driveApplication = useApi(
		async () => await getDriveApplication(driveId),
	);

	if (driveApplication.loading) return <LoadingScreen />;

	const isApplied = driveApplication?.data?.application;

	return (
		<div className='screen container'>
			<div className='text-white rounded pb-5 mt-5'>
				<DynamicForm
					response={driveApplication}
					update={data => postDriveApplication(driveId, data)}
					title={isApplied ? 'Update Drive Application' : 'Apply for Drive'}
					submitText={isApplied ? 'Update' : 'Apply'}
				/>
			</div>
		</div>
	);
}
