export const resolvePromise = async call => {
	try {
		const result = await call();
		return [result, null];
	} catch (error) {
		return [null, error];
	}
};

export const resolveTryCatch = call => {
	try {
		const result = call();
		return [result, null];
	} catch (error) {
		return [null, error];
	}
};
