import React from 'react';
import { Route, Routes, useLocation } from 'react-router-dom';
import { revokeSession } from '../api/auth';
import Footer from '../components/Footer';
import Navbar from '../components/Navbar';
import useAuth from '../hooks/useAuth';
import Dashboard from '../screens/Dashboard';
import DriveApplication from '../screens/EditDriveApplication';
import Drives from '../screens/Drives';
import LoadingScreen from '../screens/LoadingScreen';
import Login from '../screens/Login';
import NoMatch from '../screens/NoMatch';
import Profile from '../screens/Profile';

export const mainRoutes = [
	{ name: 'Dashboard', path: '', isNavItem: true },
	{ name: 'Drives', path: 'drives', isNavItem: true },
	{ name: 'Profile', path: 'profile', isNavItem: true },
];

export default function MainRouter() {
	const { student, loading, error, refresh, logout } = useAuth();
	const { pathname: currentPath } = useLocation();
	const { brand, copyright } = {
		brand: 'PLACEMENT PORTAL',
		copyright: ' 2022 Company, Inc',
	};
	const logoutSession = async () => {
		await revokeSession();
		alert('Logging out');
		logout();
	};

	if (loading) return <LoadingScreen />;

	if (error || !student) return <Login callback={refresh} />;

	return (
		<div className='bg-dark text-white'>
			<Navbar
				activePath={currentPath}
				brand={brand}
				routes={mainRoutes.filter(item => item?.isNavItem)}
				logout={logoutSession}
			/>
			<Routes>
				<Route path='/' element={<Dashboard />} />
				<Route path='/drives'>
					<Route path='' element={<Drives />} />
					<Route path=':id' element={<DriveApplication />} />
				</Route>
				<Route path='/profile' element={<Profile />} />
				<Route path='*' element={<NoMatch />} />
			</Routes>
			<Footer
				copyright={copyright}
				brand={brand}
				routes={mainRoutes.filter(item => item?.isNavItem)}
			/>
		</div>
	);
}
