import { useEffect, useState } from 'react';
import { getCurrentStudent, refreshSession } from '../api/auth';
import { clearSession } from '../utility/storage';

export default function useAuth() {
	const [loading, setLoading] = useState(true);
	const [student, setStudent] = useState();
	const [error, setError] = useState();

	const getStudent = async () => {
		setLoading(true);
		let [stu, error] = await getCurrentStudent();
		if (error) {
			if (error?.status === 403) {
				const refreshed = await refreshSession();
				console.log('refresh request', refreshed);
				if (refreshed) return await getStudent();
			}
			setError(error?.message || error);
			setStudent(null);
			setLoading(false);
			return;
		}
		setStudent(stu);
		setError(null);
		setLoading(false);
	};

	const logout = () => {
		clearSession();
		getStudent();
	};

	useEffect(() => {
		getStudent();
	}, []);

	return { student, loading, error, logout, refresh: getStudent };
}
