import React from 'react';

export default function LoadingScreen({
	screen = true,
	dark = true,
	light = false,
}) {
	let bgTone = '';
	let textTone = 'text-dark';
	if (dark) {
		bgTone = 'bg-dark';
		textTone = 'text-light';
	} else if (light) {
		bgTone = 'bg-light';
		textTone = 'text-dark';
	}
	return (
		<div className={`${bgTone} ${screen ? 'screen' : ''}`}>
			<div className={`spinner-border ${textTone}`} role='status'>
				<span className='sr-only'></span>
			</div>
		</div>
	);
}
