export const getField = field => {
	const { type: caseType, maxlength } = field;
	const type = caseType.toLowerCase();
	if (type === 'number') {
		return {
			...field,
			type: 'number',
		};
	}
	if (type === 'string' && field['enum']) {
		return {
			...field,
			type: 'select',
		};
	} else if (type === 'string') {
		if (maxlength > 70)
			return {
				...field,
				type: 'textarea',
			};
		return { ...field, type: 'text' };
	} else if (type === 'date') {
		return {
			...field,
			type: 'date',
			name: field.name + ' (mm/dd/yyyy)',
		};
	}
	return { ...field, type: 'text' };
};

export const getFieldLabel = field =>
	field.replace(/([A-Z])/g, ' $1').replace(/^./, str => str.toUpperCase());

export const getFieldPlaceHolder = field => getFieldLabel(field).toLowerCase();

export const getSchema = fields => {
	const s = {};
	fields.map(field => {
		const { name, schema } = field;
		s[name] = schema;
		return field;
	});
	return s;
};

export const getFieldsFromResponse = data =>
	Object.entries(data).map(([key, value]) => ({ name: key, value }));

export const getFieldsFromStructure = data =>
	Object.entries(data).map(([key, value]) => ({ name: key, ...value }));

export const populateFields = (d, setValue) =>
	Object.entries(d).map(([key, value]) => {
		setValue(key, value);
		return [key, value];
	});
