import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRefresh, faLink } from '@fortawesome/free-solid-svg-icons';

import { deleteDriveApplication, getDrives } from '../api/drive';
import useApi from '../hooks/useApi';
import LoadingScreen from './LoadingScreen';
import { getDateString } from '../utility/date';
import { Link } from 'react-router-dom';

export default function Drives() {
	const { data, loading, refresh } = useApi(getDrives, { pageSize: 5 });

	const onDelete = async id => {
		const [data, error] = await deleteDriveApplication(id);
		if (error) return alert(error.message);
		alert(data?.message);
		refresh();
	};

	return (
		<div className='screen'>
			<table className='table container text-center rounded bg-light text-dark'>
				<thead>
					<tr>
						<th scope='col'>Name</th>
						<th scope='col'>Field</th>
						<th scope='col'>Date</th>
						<th scope='col'>
							{loading ? (
								<LoadingScreen screen={false} dark={false} />
							) : (
								<button onClick={refresh} className='btn btn-sm btn-light'>
									<FontAwesomeIcon icon={faRefresh} /> Refresh
								</button>
							)}
						</th>
					</tr>
				</thead>
				<tbody>
					{data?.data &&
						data.data.map(
							({
								_id,
								name,
								company,
								companyLink,
								field,
								closingDate,
								driveDate,
								applied,
							}) => (
								<tr key={_id}>
									<td>
										{name} (
										<a href={companyLink} target='_blank' rel='noreferrer'>
											{company}
											<FontAwesomeIcon icon={faLink} />
										</a>
										)
									</td>
									<td>{field}</td>
									<td>
										<div>
											<span className='fw-semibold'>Closing Date: </span>
											{getDateString(closingDate)}
										</div>
										<div>
											<span className='fw-semibold'>Drive Date: </span>
											{getDateString(driveDate)}
										</div>
									</td>
									<td className='btn-group'>
										{applied ? (
											<>
												<Link to={'/drives/' + _id} className='btn btn-success'>
													Edit
												</Link>
												<button
													className='btn btn-danger'
													onClick={() => onDelete(_id)}
												>
													Cancel
												</button>
											</>
										) : (
											<Link to={'/drives/' + _id} className='btn btn-primary'>
												Apply
											</Link>
										)}
									</td>
								</tr>
							),
						)}
				</tbody>
			</table>
		</div>
	);
}
