import { EncryptStorage } from 'encrypt-storage';

const key = process.env.REACT_APP_PLACEMENT_STORAGE_KEY;
const SESSION = 'session';

const encryptStorage = new EncryptStorage(key, {
	prefix: '@PLACEMENT:',
});

const storeSession = ({ accessToken, refreshToken, expiry }) =>
	encryptStorage.setItem(SESSION, { accessToken, refreshToken, expiry });

const getSession = () => encryptStorage.getItem(SESSION);

const updateAccessToken = (accessToken, expiry) =>
	encryptStorage.setItem(SESSION, { ...getSession(), accessToken, expiry });

const clearSession = () => encryptStorage.removeItem(SESSION);

const getAccessToken = () => {
	const session = getSession();
	if (!session) return null;
	return session.accessToken;
};

const getRefreshToken = () => {
	const session = getSession();
	if (!session) return null;
	return session.refreshToken;
};

export {
	storeSession,
	updateAccessToken,
	getAccessToken,
	getRefreshToken,
	clearSession,
};
