import client from './client';
import { resolvePromise } from '../utility/functions';

const updateStudent = async data => {
	const [response, error] = await resolvePromise(() =>
		client.put(`/student`, data),
	);
	return [response?.data, error?.response?.data];
};

const getStudentStructure = async () => {
	const [response, error] = await resolvePromise(() =>
		client.get(`/student/structure`),
	);
	return [response?.data, error?.response?.data];
};

const updateResume = async resumeFile => {
	const data = new FormData();
	data.append('resume', resumeFile);
	const [response, error] = await resolvePromise(() =>
		client.post(`/student/resume`, data, {
			headers: {},
		}),
	);
	return [response?.data, error?.response?.data];
};

const getResume = async () => {
	const [response, error] = await resolvePromise(() =>
		client.get(`/student/resume`, { responseType: 'blob' }),
	);
	return [response?.data, error?.response?.data];
};

export { updateStudent, getStudentStructure, updateResume, getResume };
