import client from './client';
import { resolvePromise } from '../utility/functions';
import {
	clearSession,
	getRefreshToken,
	storeSession,
	updateAccessToken,
} from '../utility/storage';

const studentLogin = async ({ registerNumber, password }) => {
	const [response, error] = await resolvePromise(() =>
		client.post('/auth/student', { registerNumber, password }),
	);
	if (!error) {
		storeSession(response.data?.tokens);
	}
	return [response?.data?.user, error?.response?.data || error];
};

const getCurrentUser = async () => {
	const [response, error] = await resolvePromise(() => client.get('/user/me'));
	return [response?.data, error?.response?.data];
};

const getCurrentStudent = async () => {
	const [response, error] = await resolvePromise(() =>
		client.get('/student/me'),
	);
	const e = error
		? { ...error?.response?.data, status: error?.response?.status }
		: error?.response?.data;
	return [response?.data, e];
};

const refreshSession = async () => {
	const refresh_token = getRefreshToken();
	const [response, error] = await resolvePromise(() =>
		client.post('/auth/refresh', { refresh_token }),
	);
	if (!error) {
		updateAccessToken(response?.data?.accessToken, response?.data?.expiry);
		return true;
	}
	clearSession();
	return false;
};

const revokeSession = async () => {
	const refresh_token = getRefreshToken();
	const [response, error] = await resolvePromise(() =>
		client.post('/auth/revoke', { refresh_token }),
	);
	return [response?.data, error?.response?.data];
};

export {
	studentLogin,
	getCurrentUser,
	getCurrentStudent,
	refreshSession,
	revokeSession,
};
