import React from 'react';
import { getFieldLabel } from '../utility/inputs';

export default function IdCard({ student }) {
	const { data } = student;

	const getFullName = stu => `${stu?.firstName} ${stu?.lastName}`;

	return (
		<div className='container card text-dark'>
			<div className='card-body'>
				<h5 className='card-title'>
					{getFullName(data?.student)}
					<span className='badge rounded btn-lg bg-primary ms-3'>
						{data?.student?.registerNumber}
					</span>
					<span className='badge rounded btn-lg bg-secondary ms-3'>
						{data?.student?.department}
					</span>
				</h5>
				<p className='card-text'>E-mail: {data?.student?.email}</p>
				<p className='card-text'>Mobile: {data?.student?.mobileNumber}</p>
				<p className='card-text'>
					DOB: {new Date(data?.student?.dateOfBirth).toDateString()} (
					{data?.student?.gender})
				</p>
				<p className='card-text'>Passing Year: {data?.student?.passingYear}</p>
			</div>
		</div>
	);
}
