import { faCloudDownload } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { getResume, updateResume } from '../api/student';
import { displayBlob } from '../utility/window';

export default function EditResumeFile() {
	const [error, setError] = useState();
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm();

	const onSubmit = async data => {
		const [res, err] = await updateResume(data?.resume[0] || null);
		if (err) return setError(`Upload Error: ${err?.message}`);
		setError(null);
		alert(res?.message);
	};

	const downloadFile = async data => {
		const [res, err] = await getResume();
		if (err) return setError(`Download Error: ${err?.message}`);
		setError(null);
		displayBlob(res, true);
	};

	return (
		<div className='card '>
			<div className='card-header d-flex justify-content-between'>
				<h5 className='text-dark'>Resume</h5>
				<button
					onClick={downloadFile}
					className='btn btn-success btn-sm text-white'
				>
					<FontAwesomeIcon icon={faCloudDownload} className='me-2' />
					Preview
				</button>
			</div>
			<form className='form-control list-group list-group-flush text-center'>
				<li className='container d-flex list-group-item'>
					<input
						className='form-control'
						type='file'
						id='formFile'
						accept='application/pdf,application'
						{...register('resume')}
					/>
					<button onClick={handleSubmit(onSubmit)} className='btn btn-primary'>
						Upload
					</button>
				</li>
				{errors['resume'] && (
					<div className='text-danger'>{errors['resume']}</div>
				)}
				{error && <div className='text-danger'>{error}</div>}
			</form>
		</div>
	);
}
