import React, { useState } from 'react';

import { useForm } from 'react-hook-form';
import { studentLogin } from '../api/auth';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { getSchema } from '../utility/inputs';
import InputWrapper from '../components/inputs/InputWrapper';

const fields = [
	{
		label: 'Register Number',
		name: 'registerNumber',
		type: 'text',
		schema: yup.string().required().label('Register Number'),
	},
	{
		label: 'Password',
		name: 'password',
		type: 'password',
		schema: yup.string().required().label('Password'),
	},
];

const schema = yup.object(getSchema(fields)).required();

export default function Login({ callback }) {
	const [error, setError] = useState();
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm({
		resolver: yupResolver(schema),
	});

	const onSubmit = async data => {
		const [d, e] = await studentLogin(data);
		console.log(d, e);
		if (e) return setError(e.message);
		setError(null);
		if (d) callback();
	};

	return (
		<div className='screen'>
			<div className='bg-dark text-white rounded pb-5'>
				<p className='text-center mt-5 mb-0 px-5'>GCE, SALEM</p>
				<p className='text-center px-5'>PLACEMENT PORTAL</p>
				<h4 className='text-center px-5'>Login</h4>
				<form className='container px-5 py-4 text-center'>
					{fields.map(({ name, type, label, value }) => (
						<InputWrapper
							key={name}
							name={name}
							label={label}
							error={errors[name]}
						>
							<input
								className='form-control'
								type={type}
								placeholder={label.toLowerCase()}
								defaultValue={value}
								{...register(name, { required: true })}
							/>
						</InputWrapper>
					))}
					{error && <div className='form-text text-danger mb-3'>{error}</div>}
					<button className='btn btn-primary' onClick={handleSubmit(onSubmit)}>
						Login
					</button>
				</form>
			</div>
		</div>
	);
}
