import client from './client';
import { resolvePromise } from '../utility/functions';

const getDrives = async data => {
	const [response, error] = await resolvePromise(() =>
		client.get(`/drive`, { params: data }),
	);
	return [response?.data, error?.response?.data];
};

const getDriveApplications = async data => {
	const [response, error] = await resolvePromise(() =>
		client.get(`/drive_application`, { params: data }),
	);
	return [response?.data, error?.response?.data];
};

const getDriveApplication = async id => {
	const [response, error] = await resolvePromise(() =>
		client.get(`/drive_application/${id}`),
	);
	return [response?.data, error?.response?.data];
};

const postDriveApplication = async (id, data) => {
	const [response, error] = await resolvePromise(() =>
		client.post(`/drive_application/${id}`, data),
	);
	return [response?.data, error?.response?.data];
};

const deleteDriveApplication = async id => {
	const [response, error] = await resolvePromise(() =>
		client.delete(`/drive_application/${id}`),
	);
	return [response?.data, error?.response?.data];
};

export {
	getDrives,
	getDriveApplications,
	getDriveApplication,
	postDriveApplication,
	deleteDriveApplication,
};
