import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { getChunkedArray } from '../utility/arrays';
import {
	getField,
	getFieldPlaceHolder,
	populateFields,
} from '../utility/inputs';
import InputWrapper from './inputs/InputWrapper';

export default function DynamicForm({
	response: { data, error, refresh },
	update,
	title = 'Dynamic Form',
	submitText = 'Update',
}) {
	const [updateError, setUpdateError] = useState();
	const {
		register,
		handleSubmit,
		setValue,
		formState: { errors },
	} = useForm();

	const onSubmit = async data => {
		const [d, e] = await update(data);
		if (e) return setUpdateError(e.message);
		setUpdateError(null);
		const newData = await refresh();
		if (newData) populateFields(newData, setValue);
		console.log(d);
		alert(d?.message);
	};

	const getFilteredFields = (d = [], omit_array = []) =>
		d?.filter(datum => !omit_array.includes(datum?.name));

	const getInputs = ({ name, type, label, value, enum: e = [] }) => {
		if (type === 'select')
			return (
				<select
					className='form-select'
					aria-label={name}
					{...register(name)}
					defaultValue={value}
				>
					<option value=''>Select {name} here</option>
					{e.map(v => (
						<option key={v} value={v}>
							{v}
						</option>
					))}
				</select>
			);
		if (type === 'textarea')
			return (
				<textarea
					className='form-control'
					type={type}
					placeholder={getFieldPlaceHolder(label || name)}
					defaultValue={value}
					{...register(name)}
				/>
			);
		return (
			<input
				className='form-control'
				type={type}
				placeholder={getFieldPlaceHolder(label || name)}
				defaultValue={value}
				{...register(name)}
			/>
		);
	};

	return (
		<>
			<h4 className='text-center px-5 mt-5'>{title}</h4>
			<form className='container px-5 py-4 text-center'>
				<div className='row gap-5'>
					{data?.structure &&
						getChunkedArray(
							getFilteredFields(data.structure, data?.permanentFields),
						).map(chunk => (
							<div key={chunk[0]['name']} className='col-lg'>
								{chunk.map(field => {
									const updatedField = getField(field);
									const { name, label } = updatedField;
									return (
										<InputWrapper
											key={name}
											name={name}
											label={label}
											error={errors[name]}
										>
											{getInputs(updatedField)}
										</InputWrapper>
									);
								})}
							</div>
						))}
				</div>

				{error && <div className='form-text text-danger'>{error}</div>}
				{updateError && (
					<div className='form-text text-danger'>{updateError}</div>
				)}
				<button
					className='btn btn-primary mt-3'
					onClick={handleSubmit(onSubmit)}
				>
					{submitText}
				</button>
			</form>
		</>
	);
}
